<template>
  <div>
    <!--  -->
    <Top :left="left" :msg="ms2" :jizhang="jizhang" @back="back"></Top>

    <!--  -->
    <Addcategory @add="add" :title="title" :a="a"></Addcategory>
  </div>
</template>

<script>
import Top from "@/components/Top.vue";
import Addcategory from "@/components/Addcategory.vue";
import { Dialog } from "vant";

export default {
  name: "",
  props: [""],
  data() {
    return {
      jizhang: "1",
      left: "&#xe623;",
      ms2: "",
      a: "",
      title: "",
      token: ""
    };
  },

  components: { Top, Addcategory },

  computed: {},

  beforeMount() {},

  mounted() {
    this.token = localStorage.getItem("user_token");
  },

  methods: {
    back() {
      this.$router.push("/book");
    },
    add(val) {
      this.bus.$emit("loading", true);

      this.axios({
        method: "post",
        url:
          "http://jizhang-api-dev.it266.com/api/book/create?token=" +
          this.token,
        responseType: "json",
        params: {
          name: val
        }
      })
        .then(res => {
          // console.log(res);
          this.bus.$emit("loading", false);

          if (res.data.status) {
            Dialog.alert({
              message: ""
            });
            setTimeout(() => {
              Dialog.close();
              this.$router.push("/book");
            }, 1000);
            // alert("");
          } else if (res.data.data == "2") {
            Dialog.alert({
              message: "2"
            });
            setTimeout(() => {
              Dialog.close();
              this.$router.push("/book");
            }, 1000);
            // alert("2");
          } else {
          }
        })
        .catch(error => {
          // alert("");
          Dialog.alert({
            message: ""
          });
          setTimeout(() => {
            Dialog.close();
          }, 1000);
          console.log(error);
        });
    }
  },

  watch: {}
};
</script>
